import { BrowserRouter, Switch, Route } from "react-router-dom"
import Coin from './coin'
import Coins from './coins'

function Router() {
    return (
        <BrowserRouter>
            <Switch>
                <Route exact path="/">
                    <Coins />
                </Route>
                <Route path="/:coinId">
                    <Coin />
                </Route>
            </Switch>
        </BrowserRouter>
    )
}

export default Router